.homes{
    grid-column: center-start / center-end;
    margin: 15rem 0;
    
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem , 1fr));
    grid-gap: 7rem;
}

.home{
    background-color: $color-grey-light-1;
    
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 3.5rem;
    
    &__img{
        width: 100%; 
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 1;   
        grid-column-end: -1;   
        z-index: 1;
    }
    &__like{
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 2;
        grid-column-end: 3;
        fill: $color-primary;
        height: 2.5rem;
        width: 2.5rem;
        z-index: 2;
        justify-self: end;
        margin: 1rem;
    }
    &__name{
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 1;   
        grid-column-end: -1;   
        z-index: 3;
        align-self: end;
        transform: translateY(50%);
        
        width: 80%;
        justify-self: center;
        font-family: $font-display;
        font-size: 1.6rem;
        text-align: center;
        padding: 1.25rem;
        background-color: $color-secondary;
        color: #fff;
        font-weight: 400;
    }
    
    &__location,
    &__rooms{
        margin-top: 2.5rem;
    }
    
    &__location,
    &__rooms,
    &__area,
    &__price{
        font-size: 1.5rem;
        margin-left: 2rem;
        display: flex;
        align-items: center;
        
        svg{
            fill: $color-primary;
            height: 2rem;
            width: 2rem;
            margin-right: 1rem;
        }
    }
    &__btn{
        grid-column-start: 1;   
        grid-column-end: -1;   
    }
    
}